 {\n  margin: 10px 0px;\n  overflow-y: auto;\n  height: 350px;\n}\n\n.#{$prefix}tab-panel {\n  display: none;\n}\n\n.#{$prefix}tab-panel[aria-selected='true'] {\n  display: block;\n}\n\n/* Additional styling */\n.#{$prefix}template-card {\n  border: 1px solid var(--gjs-main-dark-color);\n  border-radius: 5px;\n  position: relative;\n  overflow: hidden;\n  cursor: pointer;\n\n  .gjs-thumbnail {\n    height: 150px;\n  }\n\n  &:hover .#{$prefix}controller {\n    opacity: 1;\n  }\n\n  .#{$prefix}controller {\n    position: absolute;\n    top: 0;\n    bottom: 0;\n    left: 0;\n    right: 0;\n    opacity: 0;\n    transition: opacity 0.3s ease;\n    background-color: rgba(44, 44, 44, 0.343);\n  }\n\n  .#{$prefix}controller button {\n    background-color: var(--gjs-quaternary-color);\n    color: #fff;\n    border: none;\n    padding: 5px 10px;\n    border-radius: 3px;\n    cursor: pointer;\n    position: absolute;\n    top: 50%;\n    left: 50%;\n    transform: translate(-50%, -50%);\n  }\n}\n\n/* Grid styles */\n.#{$prefix}row {\n  display: flex;\n  flex-wrap: wrap;\n  margin-left: -10px;\n  margin-top: -10px;\n}\n\n.#{$prefix}xs-12 {\n  flex: 0 0 100%;\n  max-width: 100%;\n  padding-left: 10px;\n  padding-top: 10px;\n}\n\n/* Small devices (landscape phones, 600px and up) */\n@media (min-width: 600px) {\n  .#{$prefix}sm-4 {\n    flex: 0 0 calc(33.333% - 10px); /* 4 columns with gutter */\n    max-width: calc(33.333% - 10px);\n    padding-left: 10px;\n    padding-top: 10px;\n  }\n}\n\n.#{$prefix}img {\n  max-width: 100%;\n  overflow: hidden;\n  position: relative;\n  > img {\n    display: block;\n    width: 100%;\n    height: auto;\n  }\n}\n\n.#{$prefix}separator {\n  border-color: var(--gjs-main-dark-color);\n  margin: 0;\n}\n\n.#{$prefix}no-project {\n  margin-top: 15%;\n  text-align: center;\n  font-size: var(--gjs-font-size);\n  i {\n    font-size: 50px;\n    opacity: 0.3;\n    color: var(--gjs-quaternary-color);\n  }\n}\n\n.#{$prefix}btn-delete {\n  background-color: transparent;\n  border: none;\n  color: inherit;\n  cursor: pointer;\n  padding: 0;\n}\n"],"sourceRoot":""}]);
